import Vue from 'vue'
import Router from 'vue-router'
import App from '@/App'
Vue.use(Router)

export const constantRouterMap = [{
  path: '/',
  component: App,
  redirect: 'bashboard',
  children: [{
    path: 'bashboard',
    name: 'bashboard',
    meta: {
      title: '导航页'
    },
    component: () =>
      import ('@/views/bashboard/index')
  }]
}]

export default new Router({
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  },
  routes: constantRouterMap
})



